
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsPregnancyService } from "@/apps/HTS/services/hts_pregnancy_status_service";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsPregnancyService,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsPregnancyService(
            this.patientID,
            this.providerID
          );
          this.fields = [this.htsFemalePregnancyField()];
        }
      },
      immediate: true,
    },
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData
      })
    },
    htsFemalePregnancyField(): Field {
      return {
        id: "hts_female_pregnant",
        helpText: "Select pregnancy status",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded("Pregnancy status", v.value),
          };
        },
        options: () => {
          return [
            { label: "Non-pregnant", value: "Not Pregnant / Breastfeeding" },
            { label: "Pregnant", value: "Pregnant woman" },
            { label: "Breastfeeding", value: "Breastfeeding" },
          ];
        },
      };
    },
  },
});
